import { useState, useEffect, useCallback } from 'react';
import { motion } from 'framer-motion';

interface TimeLeft {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
}

interface CountdownTimerProps {
  endDate: string;
  isActive?: boolean;
}

export function CountdownTimer({ endDate, isActive = true }: CountdownTimerProps) {
  const [timeLeft, setTimeLeft] = useState<TimeLeft>({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0
  });

  const calculateTimeLeft = useCallback(() => {
    try {
      const end = new Date(endDate).getTime();
      const now = new Date().getTime();
      const difference = Math.max(0, end - now);
      
      if (difference > 0) {
        setTimeLeft({
          days: Math.floor(difference / (1000 * 60 * 60 * 24)),
          hours: Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((difference % (1000 * 60)) / 1000)
        });
      } else {
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      }
    } catch (error) {
      console.error('Error calculating time left:', error);
      setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
    }
  }, [endDate]);

  useEffect(() => {
    if (!isActive) return;

    calculateTimeLeft();
    const timer = setInterval(calculateTimeLeft, 1000);

    return () => clearInterval(timer);
  }, [isActive, calculateTimeLeft]);

  if (!isActive) return null;

  return (
    <div className="flex justify-center gap-1.5">
      {Object.entries(timeLeft).map(([unit, value]) => (
        <motion.div
          key={unit}
          initial={{ scale: 0.5, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          className="flex flex-col items-center"
        >
          <div className="bg-white text-red-600 rounded-md p-1 min-w-[32px] text-center">
            <span className="text-sm font-bold">{value}</span>
          </div>
          <span className="text-[10px] mt-0.5 text-white/80 capitalize">{unit}</span>
        </motion.div>
      ))}
    </div>
  );
}