import { createContext, useContext, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useGlobalStore } from '@/stores/globalStore';
import { useToast } from '@/components/ui/use-toast';
import { socketManager } from '@/lib/socket';

interface Order {
  id: string;
  items: any[];
  total: number;
  status: string;
  shippingAddress: any;
  deliveryFee: number;
  createdAt: string;
}

interface OrderContextType {
  orders: Order[];
  createOrder: (orderData: any) => Promise<Order>;
  updateOrderStatus: (orderId: string, status: string) => Promise<void>;
  getOrderById: (orderId: string) => Order | undefined;
}

const OrderContext = createContext<OrderContextType | undefined>(undefined);

export function OrderProvider({ children }: { children: React.ReactNode }) {
  const [orders, setOrders] = useState<Order[]>([]);
  const navigate = useNavigate();
  const { toast } = useToast();
  const updateOrders = useGlobalStore(state => state.updateOrders);

  useEffect(() => {
    const savedOrders = localStorage.getItem('orders');
    if (savedOrders) {
      try {
        const parsedOrders = JSON.parse(savedOrders);
        setOrders(parsedOrders);
        updateOrders(parsedOrders);
      } catch (error) {
        console.error('Error loading orders:', error);
      }
    }

    socketManager.connect();

    const newOrderCleanup = socketManager.on('newOrder', (order: Order) => {
      setOrders(prev => {
        const newOrders = [order, ...prev];
        localStorage.setItem('orders', JSON.stringify(newOrders));
        updateOrders(newOrders);
        return newOrders;
      });

      toast({
        title: "Nouvelle commande",
        description: `Commande #${order.id} reçue`
      });
    });

    const statusUpdateCleanup = socketManager.on('orderStatusUpdated', ({ orderId, status }) => {
      setOrders(prev => {
        const newOrders = prev.map(order => 
          order.id === orderId ? { ...order, status } : order
        );
        localStorage.setItem('orders', JSON.stringify(newOrders));
        updateOrders(newOrders);
        return newOrders;
      });
    });

    return () => {
      newOrderCleanup();
      statusUpdateCleanup();
      socketManager.disconnect();
    };
  }, [updateOrders, toast]);

  const createOrder = async (orderData: any): Promise<Order> => {
    try {
      const orderId = Math.floor(1000 + Math.random() * 9000).toString();
      const newOrder = {
        ...orderData,
        id: orderId,
        createdAt: new Date().toISOString(),
        status: 'PENDING'
      };

      setOrders(prev => {
        const newOrders = [newOrder, ...prev];
        localStorage.setItem('orders', JSON.stringify(newOrders));
        updateOrders(newOrders);
        return newOrders;
      });

      socketManager.emit('newOrder', newOrder);

      navigate('/order-confirmation', { 
        state: { order: newOrder },
        replace: true 
      });

      return newOrder;
    } catch (error) {
      console.error('Error creating order:', error);
      throw error;
    }
  };

  const updateOrderStatus = async (orderId: string, status: string) => {
    setOrders(prev => {
      const newOrders = prev.map(order => 
        order.id === orderId ? { ...order, status } : order
      );
      localStorage.setItem('orders', JSON.stringify(newOrders));
      updateOrders(newOrders);
      return newOrders;
    });

    socketManager.emit('updateOrderStatus', { orderId, status });
  };

  const getOrderById = (orderId: string) => {
    return orders.find(order => order.id === orderId);
  };

  return (
    <OrderContext.Provider value={{
      orders,
      createOrder,
      updateOrderStatus,
      getOrderById
    }}>
      {children}
    </OrderContext.Provider>
  );
}

export function useOrder() {
  const context = useContext(OrderContext);
  if (context === undefined) {
    throw new Error('useOrder must be used within an OrderProvider');
  }
  return context;
}