import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { socketManager } from '@/lib/socket';

interface GlobalState {
  countdown: {
    isEnabled: boolean;
    endDate: string;
    title: string;
    description: string;
  };
  orders: any[];
  updateCountdown: (settings: Partial<GlobalState['countdown']>) => void;
  updateOrders: (orders: any[]) => void;
  initializeSocket: () => void;
}

export const useGlobalStore = create<GlobalState>()(
  persist(
    (set, get) => ({
      countdown: {
        isEnabled: true,
        endDate: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000).toISOString(),
        title: 'Vente Flash',
        description: 'Profitez de nos offres exceptionnelles'
      },
      orders: [],
      updateCountdown: (settings) => {
        set((state) => ({
          countdown: { ...state.countdown, ...settings }
        }));
        socketManager.emit('countdownUpdate', get().countdown);
      },
      updateOrders: (orders) => {
        set({ orders });
      },
      initializeSocket: () => {
        socketManager.connect();

        const countdownCleanup = socketManager.on('countdownUpdate', (countdown) => {
          set((state) => ({
            countdown: { ...state.countdown, ...countdown }
          }));
        });

        const ordersCleanup = socketManager.on('orderUpdate', (orders) => {
          set({ orders });
        });

        return () => {
          countdownCleanup();
          ordersCleanup();
          socketManager.disconnect();
        };
      }
    }),
    {
      name: 'babismell-storage',
      onRehydrateStorage: () => (state) => {
        if (state) {
          state.initializeSocket();
        }
      }
    }
  )
);