import { useAtomValue } from 'jotai';
import { countdownAtom } from '@/stores/countdownStore';
import { Timer } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { CountdownTimer } from './CountdownTimer';
import { useNavigate } from 'react-router-dom';

export function CountdownBanner() {
  const countdown = useAtomValue(countdownAtom);
  const navigate = useNavigate();

  if (!countdown.isEnabled) {
    return null;
  }

  return (
    <div className="w-full py-1.5 bg-gradient-to-r from-purple-600 to-purple-500">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-center gap-3">
          <div className="flex items-center gap-1.5">
            <Timer className="h-3.5 w-3.5 text-white" />
            <span className="text-xs font-medium text-white">{countdown.title}</span>
          </div>
          <CountdownTimer endDate={countdown.endDate} isActive={countdown.isEnabled} />
          <Button
            variant="ghost"
            size="sm"
            className="text-white hover:bg-white/20 text-xs py-1 px-2 h-auto"
            onClick={() => navigate('/promotions')}
          >
            Voir les promotions
          </Button>
        </div>
      </div>
    </div>
  );
}