import { createContext, useContext, useState, useEffect } from 'react';
import { socketManager } from '@/lib/socket';
import { useToast } from '@/components/ui/use-toast';

interface CountdownState {
  isEnabled: boolean;
  endDate: string;
  title: string;
  description: string;
}

interface CountdownContextType {
  settings: CountdownState;
  updateSettings: (settings: CountdownState) => Promise<void>;
  isLoading: boolean;
  error: string | null;
}

const CountdownContext = createContext<CountdownContextType | undefined>(undefined);

const INITIAL_STATE: CountdownState = {
  isEnabled: true,
  endDate: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000).toISOString(),
  title: 'Vente Flash',
  description: 'Profitez de nos offres exceptionnelles'
};

export function CountdownProvider({ children }: { children: React.ReactNode }) {
  const [settings, setSettings] = useState<CountdownState>(() => {
    try {
      const saved = localStorage.getItem('countdown_settings');
      return saved ? JSON.parse(saved) : INITIAL_STATE;
    } catch {
      return INITIAL_STATE;
    }
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();

  useEffect(() => {
    const cleanup = socketManager.on('countdownUpdate', (updatedSettings: CountdownState) => {
      setSettings(updatedSettings);
      localStorage.setItem('countdown_settings', JSON.stringify(updatedSettings));
    });

    return cleanup;
  }, []);

  const updateSettings = async (newSettings: CountdownState): Promise<void> => {
    try {
      setIsLoading(true);
      setError(null);

      const endDate = new Date(newSettings.endDate);
      if (isNaN(endDate.getTime())) {
        throw new Error('Date de fin invalide');
      }

      setSettings(newSettings);
      localStorage.setItem('countdown_settings', JSON.stringify(newSettings));
      socketManager.emit('countdownUpdate', newSettings);

      toast({
        title: "Paramètres mis à jour",
        description: "Les paramètres du compte à rebours ont été mis à jour"
      });
    } catch (error: any) {
      setError(error.message);
      toast({
        variant: "destructive",
        title: "Erreur",
        description: error.message || "Une erreur est survenue lors de la mise à jour des paramètres"
      });
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <CountdownContext.Provider value={{ 
      settings, 
      updateSettings,
      isLoading,
      error
    }}>
      {children}
    </CountdownContext.Provider>
  );
}

export function useCountdown() {
  const context = useContext(CountdownContext);
  if (!context) {
    throw new Error('useCountdown must be used within a CountdownProvider');
  }
  return context;
}